<template>
    <div id="ratingSelect">
        <div class="rating-type">
            <div @click="select(2)" class="block positive" :class="{'active': Stype===2}">
                {{desc.all}}
                <span class="count">{{ratings.length}}</span>
            </div>
            <span @click="select(0)" class="block positive" :class="{'active': Stype===0}">
                {{desc.positive}}
                 <span class="count">{{positives.length}}</span>
            </span>
            <span @click="select(1)" class="block negative" :class="{'active': Stype===1}">
                {{desc.negative}}
                 <span class="count">{{negatives.length}}</span>
            </span>
        </div>
        <div @click="toggleContent" class="switch" :class="{'on' : Ocontent}">
            <i class="iconfont icon-selectfill"></i>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script>
    const POSITIVE = 0;
    const NEGATIVE = 1;
    const ALL = 2;

    export default {
        name: "ratingSelect",
        props:{
            ratings:{
                type: Array,
                default(){
                    return [];
                }
            },
            selectType:{
                type: Number,
                default: ALL
            },
            onlyContent:{
                type: Boolean,
                default: false
            },
            desc:{
                type: Object,
                default(){
                    return{
                        all: '全部',
                        positive: '满意',
                        negative: '不满意'
                    }
                }
            }
        },
        data(){
            return{
                Stype: this.selectType,
                Ocontent: this.onlyContent
            }
        },
        methods:{
            select(type){
                this.Stype = type;
                this.$emit('Stype', type);
            },
            toggleContent(){
                this.Ocontent = !this.Ocontent;
                this.$emit('Ocontent', this.Ocontent);
            }
        },
        computed:{
            positives(){
                return this.ratings.filter((rating)=>{
                    return rating.rateType === POSITIVE;
                })
            },
            negatives(){
                return this.ratings.filter((rating)=>{
                    return rating.rateType === NEGATIVE;
                })
            }
        }
    }
</script>

